import {Dispatch} from "redux";
import {usersAPI} from "../api";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {setAppStatusAC} from "../redux/app-reducer";

export type UserInfo = {
    login: string,
    id: 1,
    node_id: string,
    avatar_url: string,
    gravatar_id: string,
    url: string,
    html_url: string,
    followers_url: string,
    following_url: string,
    gists_url: string,
    starred_url: string,
    subscriptions_url: string,
    organizations_url: string,
    repos_url: string,
    events_url: string,
    received_events_url: string,
    type: string,
    site_admin: boolean,
    name: string,
    company: string,
    blog: string,
    location: string,
    email: string,
    hireable: boolean,
    bio: string,
    twitter_username: string,
    public_repos: number,
    public_gists: number,
    followers: number,
    following: number,
    created_at: string,
    updated_at: string
}
export type RepositoryType = {
    "id": number,
    "node_id": string,
    "name": string
    "full_name": string
    "owner": Partial<UserInfo>,
    "private": boolean,
    "html_url": string
    "description": string
    "fork": boolean,
    "url": string
    "archive_url": string
    "assignees_url": string
    "blobs_url": string
    "branches_url": string
    "collaborators_url": string
    "comments_url": string
    "commits_url": string
    "compare_url": string
    "contents_url": string
    "contributors_url": string
    "deployments_url": string
    "downloads_url": string
    "events_url": string
    "forks_url": string
    "git_commits_url": string
    "git_refs_url": string
    "git_tags_url": string
    "git_url": string
    "issue_comment_url": string
    "issue_events_url": string
    "issues_url": string
    "keys_url": string
    "labels_url": string
    "languages_url": string
    "merges_url": string
    "milestones_url": string
    "notifications_url": string
    "pulls_url": string
    "releases_url": string
    "ssh_url": string
    "stargazers_url": string
    "statuses_url": string
    "subscribers_url": string
    "subscription_url": string
    "tags_url": string
    "teams_url": string
    "trees_url": string
    "clone_url": string
    "mirror_url": string
    "hooks_url": string
    "svn_url": string
    "homepage": string
    "language": null | string,
    "forks_count": number,
    "stargazers_count": number,
    "watchers_count": number,
    "size": number,
    "default_branch": string,
    "open_issues_count": number,
    "is_template": boolean,
    "topics": string[]
    "has_issues": boolean,
    "has_projects": boolean,
    "has_wiki": boolean,
    "has_pages": boolean,
    "has_downloads": boolean,
    "archived": boolean,
    "disabled": boolean,
    "visibility": string,
    "pushed_at": string
    "created_at": string
    "updated_at": string
    "permissions": {
        "admin": boolean,
        "push": boolean,
        "pull": boolean
    },
    "template_repository": null
}

const initialState = {
    user: null as null | UserInfo,
    repos: null as null | RepositoryType[]
};
export type InitialStateType = typeof initialState;

const userReducer = createSlice({
    name: 'user',
    initialState,
    reducers: {
        getUserAC(state: InitialStateType, action: PayloadAction<UserInfo>) {
            state.user = action.payload
        },
        getRepositoriesAC(state: InitialStateType, action: PayloadAction<RepositoryType[] | null>) {
            state.repos = action.payload
        },
    }
})

export const {getUserAC, getRepositoriesAC} = userReducer.actions;
export default userReducer.reducer;


export const getUserThunk = (username: string) => (dispatch: Dispatch<any>) => {
    dispatch(setAppStatusAC('loading'))
    usersAPI.getUser(username).then((res) => {
        dispatch(getUserAC(res.data))
        dispatch(getRepositoriesThunk(username))
    }).then(() => dispatch(setAppStatusAC('succeeded')))
        .catch(() => {
            dispatch(setAppStatusAC('failed'))
        })
};

export const getRepositoriesThunk = (username: string) => (dispatch: Dispatch) => {
    usersAPI.getUserRepositories(username).then((res) => {
        if (res.data.length !== 0) {
            dispatch(getRepositoriesAC(res.data))
        } else {
            dispatch(getRepositoriesAC(null))
        }
    })
        .catch(() => {
            dispatch(setAppStatusAC('failed'))
        })
};
